.yamato-common-section {
  @include pc {
    margin: 100px 0 0;
  }
  @include mq {
    margin: 60px 0 0;
  }
  .section-header {
    margin: 0;
  }
  .heading-b01 {
    margin: 0;
  }
  .collection-description {
    line-height: 2;
    @include pc {
      margin: 37px 0 0;
      font-size: 14px;
    }
    @include mq {
      margin: 29px 0 0;
      font-size: 12px;
    }
    br {
      @include mq {
        display: none;
      }
    }
  }
  .grid--view-items {
    @include pc {
      margin: 41px 0 0;
    }
    @include mq {
      margin: 30px 0 0;
    }
  }
  .grid__item {
    @include mq {
      padding-left: 0;
    }
  }
  .btn {
    border-radius: 40px;
    font-weight: bold;
    @include pc {
      width: 100%;
      max-width: 500px;
      margin: 40px 0 0;
      padding: 25px 50px;
      font-size: 18px;
    }
    @include mq {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 58px;
      margin: 30px 0 0;
      padding: 0 18px;
      font-size: 15px;
    }
  }
  .yamato-product-card {
    a {
      .yamato-product-card-container {
        @include pc {
          transition: opacity 0.3s ease-out 0s;
        }
      }
      &:hover {
        img {
          @include pc {
            opacity: 1;
          }
        }
        .yamato-product-card-container {
          @include pc {
            opacity: 0.5;
          }
        }
      }
    }
    .yamato-product-card-container {
      position: relative;
      .icon-organic-jas {
        position: absolute;
        display: block;
        left: 0;
        top: 0;
        display: block;
        z-index: 10;
        @include pc {
          width: 20%;
        }
        @include mq {
          width: 20%;
        }
      }
      .icon-rice-set {
        position: absolute;
        left: 5px;
        bottom: 5px;
        display: block;
        z-index: 10;
        @include pc {
          width: 23.85%;
        }
        @include mq {
          width: 23.125%;
        }
        img {
          display: block;
          width: 100%;
        }
      }
    }
  }
}
