.modaal-container {
  width: auto;
  border-radius: 10px;
  p,
  ol,
  ul {
    margin: 1em 0 0;
  }
  ol,
  ul {
    padding: 0 0 0 2em;
  }
  ol {
    &,
    & li {
      list-style: decimal;
    }
  }
  ul {
    &,
    & li {
      list-style: disc;
    }
  }
}
.coupon__help-display__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coupon__help-title {
  margin: 0;
  padding: 0;
  font-size: 21px;
  font-weight: 700;
  color: #ff5611;
}
.coupon__help-close {
  margin: 40px 0 0;
  border: none;
  padding: 0.5em 2em;
  border: 1px solid #ff5611;
  border-radius: 5px;
  background: #ff5611;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  @media only screen and (min-width: 751px) {
    transition: all 0.3s ease-out 0s;
    &:hover {
      background: #fff;
      color: #ff5611;
    }
  }
}
