// メディアクエリ
@mixin mq($device: handheld, $limit: max) {
  // メディア特性属性名と値の振り分け
  $width: map-get($device-width, $device);
  $prop: if($limit == max, max-width, min-width);
  $prop-value: if($limit == min, ($width + 1px), $width);

  @media only screen and (#{$prop}: $prop-value) {
    @content;
  }
}
@mixin pc {
  @include mq(handheld, min) {
    @content;
  }
}
// Hack: IE
@mixin ie {
  @media all and (-ms-high-contrast: none) {
    @content;
  }
}

// relative height
@mixin relative-height($height: 0, $device: desktop) {
  $is-valid: type-of($height) == 'number' and unit($height) == 'px';
  $height-vw: 0;

  // Base CSS
  position: relative;
  padding: $height 0 0;

  // Add vw based property;
  @if $is-valid and $height > 0 {
    $height-vw: $height / get-device-width($device) * 100vw;
    padding: $height-vw 0 0;
  }
}

// Pseudo Object
@mixin pseudo-element($url: none) {
  display: block;
  background-color: transparent;
  background-image: url($url);
  background-position: left top;
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
}

// hover action
@mixin hover-opacity {
  @include mq(handheld, min) {
    transition: opacity 0.3s ease-out 0s;
    &:hover {
      opacity: 0.5;
    }
  }
}

// dotted border (1:3)
@mixin dotted-border {
  background: transparent
    url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAACCAYAAAB7Xa1eAAAAGElEQVQImWNQE2L5rybE8p8BDTChC8AAAG3gAnnTKpqkAAAAAElFTkSuQmCC)
    left top repeat-x;
  background-size: contain;
}

// object-fit fallback
@mixin object-fit($object-fit: cover, $object-position: false) {
  $fit: 'object-fit: #{$object-fit}';
  $pos: if($object-position != false, '; object-position: #{$object-position};', '');

  object-fit: $object-fit;
  @if $object-position != false {
    object-position: $object-position;
  }
  font-family: '#{$fit}#{$pos}';
}

@mixin heading-common {
  font-family: $font-sans-serif;
  letter-spacing: 0.04em;
}
