[data-section-type='slideshow-section'] {
  &[data-device-for='desktop'] {
    @include mq {
      display: none;
    }
    .slideshow {
      @include pc {
        max-height: 70vh;
      }
    }
  }
  &[data-device-for='handheld'] {
    @include pc {
      display: none;
    }
    .slideshow {
      @include mq {
        max-height: 70vh;
      }
    }
  }
}
.slideshow {
  min-height: 0;
}
.slideshow-wrapper,
.slideshow__slide {
  @include mq {
    height: 70vh;
  }
}
.slideshow__text-wrap--mobile {
  display: none;
}
.slideshow__controls {
  display: none;
}
.slideshow__overlay {
  display: none;
}
.slideshow__text-wrap {
  &.slideshow__text-wrap--desktop {
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 630px;
    height: auto;
    padding: 30px 10px 28px;
    background-color: rgba(#5e4032, 0.8);
    z-index: 10;
    @include mq {
      padding: 20px 10px;
    }
    .slideshow__text-content {
      position: relative;
      transition: none;
    }
  }
}
.slideshow__slide--active {
  .slideshow__text-content {
    &.slideshow__text-content--vertical-center {
      transform: none;
    }
  }
}
.slideshow__title,
.slideshow__subtitle {
  text-shadow: none;
  @include mq {
    display: block;
  }
}
.slideshow__title {
  margin: 0;
  font-family: $font-sans-serif;
  font-feature-settings: 'pkna';
  @include pc {
    font-size: 36px;
    font-weight: bold;
  }
  @include mq {
    font-size: 22px;
  }
}
.slideshow__subtitle {
  display: block;
  padding: 0 20px;
  @include pc {
    margin: 8px 0 0;
    letter-spacing: 0.06em;
    font-size: 22px;
    font-weight: bold;
  }
  @include mq {
    margin: 9px 0 0;
    font-size: 14px;
  }
}
.slideshow__text-content {
  opacity: 1;
}
