.site-footer__banner {
  max-width: 1060px;
  @include pc {
    margin: 140px 0 0 auto;
  }
  @include mq {
    margin: 24px 0 0;
  }
  .__pc {
    @include mq {
      display: none;
    }
  }
  .__sp {
    @include pc {
      display: none;
    }
  }
  ul {
    display: flex;
    justify-content: flex-start;
    @include pc {
      margin: 0 -18px;
    }
    @include mq {
      flex-wrap: wrap;
      margin: 0;
    }
  }
  li {
    @include pc {
      flex: 1 1 350px / 1122px * 100%;
      margin: 0 18px;
    }
    @include mq {
      flex: 0 1 47.3%;
      margin: 16px 0 0;
    }
    &:nth-of-type(2n) {
      @include mq {
        margin-left: 5.4%;
      }
    }
  }
  a {
    display: block;
  }
  img {
    vertical-align: bottom;
  }
}
