.yamato-commitment {
  @include pc {
    margin: 91px 0 0;
  }
  @include mq {
    margin: 40px 0 0;
  }
  > .feature-row {
    @include pc {
      margin: 37px 0 0;
    }
    @include mq {
      margin: 30px 0 0;
    }
    .feature-row {
      @include pc {
        display: flex;
        align-items: center;
      }
      @include mq {
        display: block;
      }
    }
  }
  .feature-row__item {
    &.__figure {
      @include pc {
        flex: 1 1 auto;
        width: 50%;
      }
    }
  }
  .feature-row__image-wrapper {
    margin-bottom: 0;
  }
  .feature-row__text {
    padding: 0;
    box-shadow: 1px 1px 6px rgba(#000000, 0.16);
    background: map-get($color, white);
    @include pc {
      position: relative;
      width: 53%;
      margin: 0 0 0 -3%;
      z-index: 10;
    }
    h2 {
      margin: 0;
      background-color: #ff7600;
      color: map-get($color, white);
      letter-spacing: 0.09em;
      font-family: $font-sans-serif;
      @include pc {
        padding: 20px 45px 20px 30px;
        font-size: 21px;
      }
      @include mq {
        padding: 17px 20px;
        font-size: 15px;
      }
      small {
        @include pc {
          font-size: 18px;
        }
        @include mq {
          font-size: 12px;
        }
      }
    }
  }
  .featured-row__subtext {
    margin: 0;
    @include pc {
      padding: 30px 45px 38px;
      font-size: 14px;
    }
    @include mq {
      padding: 20px 20px 24px;
      font-size: 14px;
    }
  }
  .featured-row__button {
    text-align: center;
    @include pc {
      margin: 60px 0 0;
    }
    @include mq {
      margin: 30px 0 0;
    }
    .btn {
      border-radius: 40px;
      font-weight: bold;
      @include pc {
        width: auto;
        padding: 26px 50px;
        font-size: 18px;
      }
      @include mq {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 58px;
        padding: 0 18px;
        font-size: 16px;
      }
      br {
        @include pc {
          display: none;
        }
      }
    }
  }
}
