.medium-up--push-one-twelfth {
  @include pc {
    left: 0;
  }
}
.medium-up--five-sixths {
  @include pc {
    width: 100%;
  }
}
.yamato-blog-article {
  .section-header {
    margin: 0;
  }
  .article__eye-catch {
    @include pc {
      margin: 35px 0 0;
    }
    @include mq {
      margin: 25px 0 0;
    }
    img {
      display: block;
      width: 100%;
    }
  }
  .rte {
    line-height: 1.6667;
    @include pc {
      margin: 40px 0 0;
      font-size: 14px;
    }
    @include mq {
      margin: 30px 0 0;
      font-size: 12px;
    }
  }
  .article__title {
    font-family: $font-sans-serif;
    &-prop {
      display: flex;
      align-items: center;
      font-weight: normal;
      @include pc {
        font-size: 14px;
      }
      @include mq {
        font-size: 12px;
      }
    }
    .article__tag-icon {
      border-radius: 15px;
      @include pc {
        min-width: 90px;
        margin: 0 0 0 12px;
        padding: 8px;
      }
      @include mq {
        min-width: 72px;
        margin: 0 0 0 7px;
        padding: 4px 6px;
      }
      &.__new {
        background: #009b4a;
        color: white;
      }
    }
    &-main {
      @include heading-common;
      display: block;
      text-align: left;
      font-weight: bold;
      @include pc {
        margin: 20px 0 0;
        font-size: 28px;
      }
      @include mq {
        margin: 20px 0 0;
        font-size: 20px;
      }
    }
  }
  .btn-wrapper {
    text-align: center;
    @include pc {
      margin: 60px 0 0;
    }
    @include mq {
      margin: 50px 0 0;
    }
  }
  .btn {
    @include heading-common;
    background: var(--color-btn-primary);
    color: white;
    border-radius: 40px;
    font-weight: bold;
    @include pc {
      display: block;
      max-width: 400px;
      margin: 0 auto;
      border-color: #ff5611;
      padding: 26px;
      font-size: 18px;
      transition: all 0.3s ease-out 0s;
      transition-property: background-color, color;
    }
    @include mq {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 58px;
      margin: 0 auto;
      padding: 0 18px;
      font-size: 16px;
    }
    &:hover {
      @include pc {
        background-color: #fff;
        color: #ff5611;
      }
    }
  }
  .blog-tag-nav {
    @include pc {
      margin: 100px 0 0;
    }
    @include mq {
      margin: 60px 0 0;
    }
    .blog-tag-nav__title {
      @include heading-common;
      border-bottom: 1px solid #e2e2e2;
      color: var(--color-btn-primary);
      font-weight: normal;
      @include pc {
        padding: 0 30px 20px;
        font-size: 18px;
      }
      @include mq {
        padding: 0 0 13px;
        font-size: 16px;
      }
    }
    ul {
      display: flex;
      @include pc {
        margin: 20px 0 0;
        padding: 0 30px;
        font-size: 14px;
      }
      @include mq {
        margin: 10px 0 0;
        padding: 0;
        font-size: 12px;
      }
    }
    li {
      margin: 0 40px 0 0;
    }
  }
}
