// elements
.heading-a01 {
  @include heading-common;
  font-weight: normal;
  @include mq(handheld, min) {
    font-size: 18px;
  }
  @include mq(handheld) {
    font-size: 16px;
  }
}

.heading-b01 {
  @include heading-common;
  text-align: center;
  font-weight: bold;
  @include mq(handheld, min) {
    font-size: 28px;
  }
  @include mq(handheld) {
    font-size: 20px;
  }
  .heading-icon {
    display: block;
    text-align: center;
  }
  .heading-label {
    display: block;
    margin: 12px 0 0;
  }
  small {
    display: block;
    margin: 7px 0 0;
    color: var(--color-btn-primary);
    font-weight: normal;
    letter-spacing: 0.05em;
    @include mq(handheld, min) {
      font-size: 14px;
    }
    @include mq(handheld) {
      font-size: 11px;
    }
  }
}
