.vegitable-carousel,
.vegitable-carousel-arrow {
  margin: 100px 0 0;
  p {
    height: 200px;
  }
  img {
    width: auto;
    height: 100%;
    vertical-align: bottom;
  }
  .slick-arrow::before {
    font-size: 24px;
    font-weight: bold;
    color: #ff5611;
  }
  .slick-next::before {
    font-weight: bold;
    content: '>';
  }
  .slick-prev::before {
    content: '<';
  }
}
