.yamato-rotate-blog {
  * {
    min-width: 0;
    min-height: 0;
  }
  display: flex;
  background: var(--color-btn-primary);
  &,
  a {
    color: map-get($color, white);
  }
  h2 {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    @include heading-common;
    @include pc {
      flex: 0 0 180px;
      font-size: 18px;
    }
    @include mq {
      flex: 0 0 80px;
      font-size: 12px;
    }
  }
  .yamato-rotate-blog-list {
    flex: 1 1 auto;
    border-left: 1px solid map-get($color, white);
    @include pc {
      padding: 0 107px;
      font-size: 14px;
    }
    @include mq {
      padding: 0 36px;
      font-size: 12px;
    }
    li {
      a {
        @include pc {
          padding: 24px 0 22px;
        }
        @include mq {
          padding: 17px 0;
        }
        span {
          margin: 0;
        }
      }
    }
  }
  .slick-prev {
    @include pc {
      left: -50px;
    }
    @include mq {
      left: -25px;
    }
    &::before {
      content: '<';
      @include pc {
        font-size: 14px;
      }
      @include mq {
        font-size: 12px;
      }
    }
  }
  .slick-next {
    @include pc {
      right: -50px;
    }
    @include mq {
      right: -25px;
    }
    &::before {
      content: '>';
      @include pc {
        font-size: 14px;
      }
      @include mq {
        font-size: 12px;
      }
    }
  }
}
