.yamato-yaoya {
  p {
    @include mq {
      font-size: 1em;
    }
  }
  .btn {
    @include pc {
      border-color: #ff5611;
      font-family: $font-sans-serif;
      transition: all 0.3s ease-out 0s;
      transition-property: background-color, color;
    }
    &:hover {
      @include pc {
        background-color: #fff;
        color: #ff5611;
      }
    }
  }
  .btn-wrapper {
    .btn {
      display: block;
      width: 100%;
    }
  }
  input {
    &[type='text'] {
      @include mq {
        -webkit-appearance: none;
      }
    }
  }
  &__main-container {
    @include mq(handheld, min) {
      display: flex;
    }
    @include mq(handheld) {
      display: block;
      padding-top: 50px;
    }
    > #shopify-section-header {
      @include mq(handheld, min) {
        flex: 0 0 380px;
      }
    }
    > .page-container {
      @include mq(handheld, min) {
        flex: 1 1 auto;
      }
      .main-content {
        @include pc {
          padding: 0;
        }
        @include mq {
          padding: 0;
        }
      }
      .index-section {
        @include pc {
          margin: 0;
          padding: 0;
        }
        @include mq {
          margin: 0;
          padding: 0;
        }
      }
    }
  }

