%list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

%basic-field {
  width: 100%;
  min-height: 44px;
  margin: 0;
  border: none;
  border-radius: 2px;
  padding: 8px 15px;
  background-color: #f5f5f5;
  color: #261204;
}
%basic-field-container {
  @include mq(handheld) {
    margin: 20px 20px 0;
  }
}
