.site-footer-popular {
  @include pc {
    display: none;
  }
  @include mq {
    margin: 70px 0 0;
  }
  .heading-a01 {
    @include mq {
      @include heading-common;
      margin: 0;
      font-size: 19px;
      font-weight: bold;
    }
  }
  .popular-list {
    @include mq {
      display: flex;
      margin: 20px -5px 0;
    }
    li {
      @include mq {
        flex: 1;
        margin: 0 5px;
      }
      .icon-popular {
        &::before {
          @include mq {
            width: 18px;
            padding: 0;
            font-size: 12px;
          }
        }
      }
      .list-view-item__link {
        @include mq {
          display: block;
        }
      }
    }
  }
  .list-view-item__image-wrapper {
    @include mq {
      padding: 58px / 105px * 100% 0 0;
    }
    img {
      @include mq {
        @include object-fit;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .list-view-item__title {
    @include mq {
      margin: 8px 0 0;
      line-height: 1.4;
      font-size: 12px;
    }
  }
  .yamato-product-card-container {
    .icon-organic-jas {
      @include mq {
        position: absolute;
        right: 0;
        top: 0;
        width: 27.25%;
        z-index: 10;
      }
    }
    img {
      @include mq {
        display: block;
        width: 100%;
      }
    }
    .icon-rice-set {
      @include mq {
        position: absolute;
        bottom: 2px;
        left: 2px;
        width: 27.25%;
        z-index: 10;
      }
      img {
        @include mq {
          display: block;
          width: 100%;
        }
      }
    }
  }
}
