.global-nav {
  @include mq(handheld, mq) {
    margin: 35px 0 0;
  }
  @include mq(handheld) {
    margin: 20px 20px 0;
  }
  ul {
    margin: 0;
  }
  li {
    &::after {
      @include dotted-border;
      display: block;
      height: 1px;
      content: '';
    }
    &:last-of-type {
      &::after {
        content: none;
      }
    }
  }
  a {
    padding: 0;
    @include pc {
      transition: color 0.3s ease-out 0s;
    }
    &:hover {
      text-decoration: none;
      @include pc {
        color: #ff5611;
      }
    }
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include mq(handheld, min) {
      min-height: 60px;
    }
    @include mq(handheld) {
      min-height: 50px;
    }
    &:hover {
      .site-nav__label {
        @include mq(handheld, min) {
          margin-right: 16px;
          svg {
            &.yamato-glonav-arrow {
              fill: #ff5611;
            }
          }
        }
      }
    }
    .site-nav__label {
      svg {
        &.yamato-glonav-arrow {
          fill: #261204;
        }
      }
    }
  }
  // nav heading
  .heading-a01 {
    @include heading-common;
    border-radius: 6px 6px 0 0;
    background-color: var(--color-btn-primary);
    color: var(--color-btn-primary-text);
    @include mq(handheld, min) {
      margin: 30px 0 0;
      padding: 14px 15px 13px;
      font-weight: bold;
    }
    @include mq(handheld) {
      margin: 10px 0 0;
      padding: 10px 15px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
}
.site-nav__icon {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 0 1 46px;
}
.site-nav__label {
  flex: 1 1 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 21px;
  border: none;
  transition: margin-right 0.3s ease-out 0s;
}
