.site-header {
  position: static;
  @include mq(handheld, min) {
    padding: 26px 40px 0 58px;
  }
  @include mq(handheld) {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    align-items: stretch;
    padding: 0;
    z-index: 100;
  }
  // logo
  &__logo {
    margin: 0;
    @include mq(handheld) {
      box-sizing: content-box;
      width: 125px;
      padding: 14px 16px 10px;
    }
  }
  &__logo-symbol {
    @include mq(handheld, min) {
      display: block;
      width: 168px;
      margin: 0 auto;
    }
    @include mq(handheld) {
      display: none;
    }
  }
  &__logo-type {
    display: block;
    width: 100%;
    @include mq(handheld, min) {
      margin: 15px auto 0;
    }
  }
  .yamato-global-header {
    overflow: hidden;
    @include mq(handheld, min) {
      margin: 31px 0 0;
    }
    @include mq(handheld) {
      position: fixed;
      top: 50px;
      right: 0;
      width: 0;
      height: 100%;
      height: 100vh;
      height: calc(100vh - 50px);
      border-top: 1px solid #e2e2e2;
      background-color: map-get($color, white);
      z-index: 100;
      opacity: 0;
      transition: all 0.3s ease-out 0s;
    }
  }
  .yamato-global-header__container {
    @include mq(handheld) {
      width: 100%;
      height: 100vh;
      height: calc(100vh - 50px);
      overflow: auto;
    }
  }
  &__banner {
    @include mq(handheld, min) {
      margin: 40px 0 0;
    }
    @include mq(handheld) {
      margin: 30px 20px 240px;
    }
    ul {
      @include mq(handheld) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }
    }
    li {
      @include mq(handheld, min) {
        margin: 30px 0 0;
        &:first-child {
          margin-top: 0;
        }
      }
      @include mq(handheld) {
        width: 160px;
        margin: 15px 0 0 15px;
        &:nth-child(-n + 2) {
          margin-top: 0;
        }
        &:nth-child(2n + 1) {
          margin-left: 0;
        }
      }
    }
    // a {
    //   display: block;
    // }
    img {
      vertical-align: bottom;
    }
  }
  .site-header__popular {
    @include mq(handheld) {
      display: none;
    }
  }
  .global-nav-drawer-controller {
    display: none;
    @include mq(handheld) {
      position: relative;
      display: block;
      width: 50px;
      margin: 0;
      background-color: var(--color-btn-primary);
      color: map-get($color, white);
      &::after {
        position: absolute;
        bottom: 4px;
        display: block;
        width: 100%;
        text-align: center;
        text-transform: uppercase;
        font-size: 10px;
        content: 'Menu';
      }
    }
    &__content {
      @include mq(handheld) {
        position: relative;
        display: block;
        width: 24px;
        height: 18px;
        margin: 11px auto 0;
        &::before,
        &::after {
          position: absolute;
          left: 0;
          display: block;
          width: 100%;
          height: 1px;
          background-color: map-get($color, white);
          content: '';
          transition: transform 0.3s ease-out 0s;
        }
        &::before {
          top: 0;
        }
        &::after {
          bottom: 0;
        }
      }
      > span {
        @include mq(handheld) {
          position: absolute;
          top: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          margin: auto;
          background-color: map-get($color, white);
          opacity: 1;
          transition: opacity 0.3s ease-out 0s;
        }
      }
    }
  }
  .site-header__icon.site-header__cart {
    opacity: 0;
  }
}
#global-nav-flag:checked {
  & + .site-header {
    .yamato-global-header {
      @include mq(handheld) {
        width: 100%;
        max-width: 375px;
        opacity: 1;
      }
    }
    .yamato-global-header__container {
      @include mq {
        width: 100%;
      }
    }
    .global-nav-drawer-controller {
      &::after {
        @include mq(handheld) {
          content: 'close';
        }
      }
      &__content {
        &::before {
          transform: rotate(45deg) translate(6px, 6px);
        }
        &::after {
          transform: rotate(-45deg) translate(6px, -6px);
        }
        > span {
          @include mq(handheld) {
            opacity: 0;
          }
        }
      }
    }
  }
}
