body {
  margin: 0;
  padding: 0;
  color: map-get($color, body-text);
  font-size: 1rem;
  font-family: $font-sans-serif;
}
.ui-controller {
  display: none;
}
.is-drawer-enabled {
  height: 100%;
  overflow: hidden;
}
img {
  max-width: 100%;
}
a {
  &[href^='tel:'] {
    @include mq(handheld, min) {
      pointer-events: none;
      text-decoration: none;
    }
  }
  img {
    @include mq(handheld, min) {
      transition: opacity 0.3s ease-out 0s;
    }
  }
  &:hover {
    img {
      @include mq(handheld, min) {
        opacity: 0.5;
      }
    }
  }
}
.svg-sprite {
  display: none;
}
