.popular-group {
  @include pc {
    display: flex;
    justify-content: space-between;
    margin: 40px 0 0;
  }
  @include mq {
    display: block;
    margin: 30px 0 0;
  }
  .popular-impressed {
    position: relative;
    @include pc {
      flex: 1 1 820px;
    }
    .yamato-product-card-container {
      .icon-organic-jas {
        right: 0;
        left: auto;
        @include pc {
          width: 8.54%;
        }
      }
      .icon-rice-set {
        @include pc {
          width: 9.76%;
        }
        @include mq {
          width: 18.127%;
        }
      }
    }
    .popular-impressed-icon {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      @include mq {
        width: 34px;
      }
    }
    .yamato-product-card-info {
      display: none;
    }
  }
  .yamato-product-card {
    a {
      display: block;
    }
  }
  .yamato-product-card-image-wrapper {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 465px / 820px * 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
  .popular-list {
    counter-reset: content-popular;
    @include pc {
      flex: 1 1 259px;
      margin: 0 0 0 40px;
    }
    ul {
      @include pc {
        margin: 0;
      }
      @include mq {
        display: flex;
        justify-content: space-between;
        margin: 10px 0 0;
      }
    }
    li {
      counter-increment: content-popular;
      position: relative;
      @include pc {
        margin: 12px / 259px * 100% 0 0;
      }
      @include mq {
        flex: 1 1 105px;
        margin: 0 0 0 10px;
      }
      &:first-child {
        @include pc {
          margin: 0;
        }
        @include mq {
          margin: 0;
        }
      }
      .icon-popular {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        &::before {
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 10;
          content: counter(content-popular);
          @include pc {
            width: 30px;
            padding: 10px 0;
            font-size: 16px;
          }
          @include mq {
            width: 18px;
            padding: 2px 0;
            font-size: 12px;
          }
        }
        .icon-popular-footer {
          &::before,
          &::after {
            position: absolute;
            top: 100%;
            display: block;
            border-color: transparent;
            border-style: solid;
            content: '';
          }
          &::before {
            left: 0;
            @include pc {
              border-width: 0 0 5px 15px;
            }
            @include mq {
              border-width: 0 0 3px 9px;
            }
          }
          &::after {
            right: 0;
            @include pc {
              border-width: 0 15px 5px 0;
            }
            @include mq {
              border-width: 0 9px 3px 0;
            }
          }
        }
      }
      &:first-child {
        span.icon-popular {
          &::before {
            background-color: map-get($color, prize1st);
            color: map-get($color, white);
          }
          .icon-popular-footer {
            &::before {
              border-left-color: map-get($color, prize1st);
            }
            &::after {
              border-right-color: map-get($color, prize1st);
            }
          }
        }
      }
      &:nth-child(2) {
        span.icon-popular {
          &::before {
            background-color: map-get($color, prize2nd);
            color: map-get($color, white);
          }
          .icon-popular-footer {
            &::before {
              border-left-color: map-get($color, prize2nd);
            }
            &::after {
              border-right-color: map-get($color, prize2nd);
            }
          }
        }
      }
      &:nth-child(3) {
        span.icon-popular {
          &::before {
            background-color: map-get($color, prize3rd);
            color: map-get($color, white);
          }
          .icon-popular-footer {
            &::before {
              border-left-color: map-get($color, prize3rd);
            }
            &::after {
              border-right-color: map-get($color, prize3rd);
            }
          }
        }
      }
    }
    .yamato-product-card {
      position: relative;
      .icon-organic-jas {
        position: absolute;
        display: block;
        right: 0 !important;
        left: auto !important;
        top: 0;
        display: block;
        z-index: 10;
        @include mq {
          width: 20%;
        }
      }
    }
    .yamato-product-card-image-wrapper {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 142px / 249px * 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        @include object-fit;
      }
    }
    .yamato-product-card-info {
      display: none;
    }
  }
}
.yamato-product-card-outbound {
  .yamato-product-card-info {
    @include pc {
      margin: 24px 0 0;
    }
    @include mq {
      margin: 18px 0 0;
    }
    dt {
      @include heading-common;
      font-weight: bold;
      @include pc {
        font-size: 21px;
      }
      @include mq {
        font-size: 15px;
      }
    }
    dd {
      @include pc {
        margin: 4px 0 0;
        font-size: 14px;
      }
      @include mq {
        margin: 4px 0 0;
        font-size: 14px;
      }
    }
    .price-item {
      font-weight: normal;
      @include pc {
        font-size: 16px;
      }
      @include mq {
        font-size: 14px;
      }
    }
  }
}
