.footer-guide {
    @include mq(handheld, min) {
        margin: 110px 0 0;
        padding: 0 58px;
    }
    @include mq(handheld) {
        display: none;
    }
    &-content {
        @include mq(handheld, min) {
            max-width: 1484px;
            margin: 0 auto;
        }
    }
}
.footer-guide-layout {
    @include mq(handheld, min) {
        display: flex;
        margin: 50px 0 0;
        border-right: 1px solid #261204;
    }
}
.footer-guide--column {
    @include mq(handheld, min) {
        flex: 1;
        border-left: 1px solid #261204;
        padding: 0 48px 10px;
    }
}
.heading-footer-guide01 {
    @include heading-common;
    @include mq(handheld, min) {
        display: flex;
        align-items: flex-start;
        margin: 40px 0 0;
        font-size: 20px;
        font-weight: bold;
        &::before {
            flex: 0 0 auto;
            display: block;
            width: 0.95em;
            height: 0.95em;
            margin: 3px 0.25em 0 0;
            background: #261204;
            content: '';
        }
    }
    &:first-child {
        margin-top: 0;
    }
}
.heading-footer-guide02 {
    @include heading-common;
    @include mq(handheld, min) {
        margin: 20px 0 0;
        font-size: 16px;
    }
    &:first-child {
        margin-top: 0;
    }
}
.text-footer-guide {
    @include mq(handheld, min) {
        margin: 12px 0 0;
        font-size: 12px;
    }
}
.table-footer-guide {
    @include mq(handheld, min) {
        margin: 20px 0 0;
        border: none;
        border-collapse: collapse;
        font-size: 12px;
    }
    &:first-child {
        margin-top: 0;
    }
    th,
    td {
        font-family: $font-sans-serif;
        font-weight: normal;
    }
    thead {
        th {
            @include pc {
                border: 1px solid map-get($color, white);
                border-width: 0 0 0 1px;
                background-color: var(--color-btn-primary);
                color: map-get($color, white);
            }
            &:first-child {
                @include pc {
                    border: none;
                    width: 50%;
                }
            }
        }
    }
    tbody {
        th,
        td {
            @include pc {
                border: 1px solid #a0a0a0;
            }
        }
        tr {
            &:first-child {
                th,
                td {
                    @include pc {
                        border-top: none;
                    }
                }
            }
        }
    }
}
.note-list-footer-guide {
    @include pc {
        margin: 10px 0 0;
        font-size: 12px;
    }
    li {
        @include pc {
            padding-left: 1em;
            text-indent: -1em;
        }
        &::before {
            content: '\203B';
        }
    }
}
.define-list-footer-guide {
    @include pc {
        margin: 20px 0 0;
        font-size: 12px;
    }
    dl,
    dt,
    dd {
        margin: 0;
    }
    dl {
        @include pc {
            display: flex;
        }
    }
    dt {
        @include pc {
            flex: 0 1 7em;
        }
    }
    dd {
        @include pc {
            flex: 1 1 auto;
        }
    }
}
