.site-footer {
  background-color: #000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  &,
  a {
    color: map-get($color, white);
  }
  @include pc {
    margin: 120px 0 0;
    padding: 104px 0 30px;
  }
  @include mq {
    margin: 50px 0 0;
    padding: 53px 20px 25px;
  }
}
.site-footer__content {
  display: block;
  @include mq {
    padding: 0;
  }
}
.site-footer__logo {
  margin: 0 auto;
  @include pc {
    width: 283px;
  }
  @include mq {
    width: 230px;
  }
  img {
    display: block;
    width: 100%;
  }
}
.site-footer__item {
  display: block;
  padding: 0;
  @include pc {
    margin: 0;
  }
  &:first-of-type {
    @include pc {
      margin-top: 56px;
    }
    @include mq {
      margin-top: 37px;
    }
  }
  &:nth-of-type(n + 2) {
    @include mq {
      display: none;
    }
  }
}
.site-footer-item-tall {
  @include pc {
    height: auto;
    margin: 0;
  }
}
.site-footer__linklist {
  display: flex;
  flex-wrap: wrap;
  letter-spacing: 0.04em;
  font-size: 12px;
  @include pc {
    justify-content: center;
    line-height: 2.05;
  }
  @include mq {
    max-width: 335px;
    margin: 0 auto;
  }
  &::before {
    @include mq {
      display: block;
      width: 100%;
      height: 1px;
      background: transparent
        url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAFElEQVQImWNgoAP4DwXo4ky4NAAAa2sH+6qA8T0AAAAASUVORK5CYII=)
        left bottom repeat-x;
      background-size: 3px;
      content: '';
    }
  }
  > li {
    @include pc {
      flex: 0 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 0 0.5em;
      padding: 0;
    }
    @include mq {
      flex: 1 1 50%;
      padding: 0;
      &::after {
        @include mq {
          display: block;
          width: 100%;
          height: 1px;
          background: transparent
            url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAFElEQVQImWNgoAP4DwXo4ky4NAAAa2sH+6qA8T0AAAAASUVORK5CYII=)
            left bottom repeat-x;
          background-size: 3px;
          content: '';
        }
      }
    }
    &::before {
      @include pc {
        margin: 0 0.5em 0 0;
        content: '/';
      }
    }
    &:first-child {
      @include pc {
        margin-left: 0;
      }
      &::before {
        @include pc {
          content: none;
        }
      }
    }
    &:nth-of-type(2n + 1) {
      > a {
        &::before {
          @include mq {
            content: none;
          }
        }
      }
    }
    &:nth-of-type(n + 6) {
      @include pc {
        display: none;
      }
    }
    > a {
      @include mq {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        padding: 18px 10px 20px;
        &::before {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          display: block;
          width: 1px;
          background: transparent
            url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAFElEQVQImWNgoAP4DwXo4ky4NAAAa2sH+6qA8T0AAAAASUVORK5CYII=)
            left top repeat-y;
          background-size: 3px;
          content: '';
        }
      }
      > span {
        margin: 0 10px 0 0;
      }
      > svg {
        @include pc {
          display: none;
        }
        @include mq {
          margin: 0 0 0 auto;
        }
      }
    }
  }
}
.site-footer__copyright {
  text-align: center;
  letter-spacing: 0.04em;
  @include pc {
    margin: 96px 0 0;
    font-size: 12px;
  }
  @include mq {
    margin: 33px 0 0;
    font-size: 9px;
  }
}
