.collection-hero__title,
.commitment-block,
.commitment-calender,
.overview-block {
  .__pc {
    @include mq {
      display: none;
    }
  }
  .__sp {
    @include pc {
      display: none;
    }
  }
}

.commitment-block {
  @include pc {
    margin: 150px 0 0;
  }
  @include mq {
    margin: 60px 0 0;
  }
  &:first-child {
    margin: 0;
  }
  &.__reverse {
    .commitment-block__content {
      flex-direction: row-reverse;
    }
  }
  &__button {
    display: flex;
    justify-content: center;
    margin: 80px 0 0;
    .btn {
      padding: 26px 50px;
      font-size: 18px;
      border-radius: 40px;
    }
  }
  // &.__1st {
  //   .__header-icon {
  //     @include pc {
  //       width: 162px;
  //     }
  //     @include mq {
  //       width: 126px;
  //     }
  //   }
  // }
  // &.__2nd {
