  //     @include pc {
  //       width: 165px;
  //     }
  //     @include mq {
  //       width: 127px;
  //     }
  //   }
  // }
  // &.__4th {
  //   .__header-icon {
  //     @include pc {
  //       width: 100px;
  //     }
  //     @include mq {
  //       width: 65px;
  //     }
  //   }
  // }
  &__content {
    @include pc {
      display: flex;
      justify-content: space-between;
      margin: 42px -21px 0;
    }
    @include mq {
      margin: 20px 0 0;
    }
    &:first-child {
      @include pc {
        margin-top: 0;
      }
    }
  }
  &__header {
    display: flex;
    justify-content: space-between;
    @include mq {
      margin: 0 0 20px;
    }
  }
  &__title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
    color: var(--color-btn-primary);
    font-family: $font-sans-serif;
    .__header {
      border-bottom: 2px solid var(--color-btn-primary);
      letter-spacing: -0.01em;
      line-height: 0.85;
      font-family: 'Arial', 'Helvetica', sans-serif;
      font-weight: normal;
      @include pc {
        padding: 0 0 10px;
        font-size: 46px;
      }
      @include mq {
        padding: 0 0 4px;
        font-size: 36px;
      }
    }
    .__label {
      @include pc {
        margin: 20px 0 0;
        letter-spacing: 0.06em;
        font-size: 28px;
      }
      @include mq {
        margin: 20px 0 0;
        letter-spacing: 0.04em;
        font-size: 20px;
      }
    }
    .__lead {
      @include pc {
        margin: 15px 0 0;
        letter-spacing: 0.06em;
        font-size: 18px;
      }
      @include mq {
        margin: 10px 0 0;
        letter-spacing: 0.04em;
        font-size: 15px;
      }
    }
  }
  .__header-icon {
    flex: 0 0 auto;
    @include pc {
      width: 200px;
    }
    @include mq {
      width: 156px;
    }
    img,
    svg {
      display: block;
      width: 100%;
    }
    svg {
      fill: rgba(#9e8b4b, 0.45);
    }
  }
  p {
    line-height: 1.7;
    letter-spacing: 0.06em;
    @include pc {
      margin: 30px 0 0;
      font-size: 14px;
    }
    @include mq {
      margin: 20px 0 0;
      font-size: 12px;
    }
    &:first-child {
      margin: 0;
    }
  }
  .__figure {
    @include pc {
      margin: 42px 0 0;
    }
    @include mq {
      margin: 20px 0 0;
    }
    img {
      display: block;
    }
    &:first-child {
      margin: 0;
    }
    &.__fill {
      @include pc {
        height: 100%;
        img {
          @include object-fit;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  &__column {
    @include pc {
      flex: 0 1 540px;
      margin: 0 21px;
    }
  }
}
.commitment-calender {
  @include pc {
    margin: 100px 0 0;
  }
  @include mq {
    width: auto;
    overflow: auto;
    margin: 20px -22px 0 0;
  }
  &__content {
    @include mq {
      width: 1043px;
    }
  }
  img {
    display: block;
  }
}
.overview-block {
  @include pc {
    margin: 40px 0 0;
  }
  & + .overview-block {
    @include pc {
      margin: 120px 0 0;
    }
    @include mq {
      margin: 20px 0 0;
    }
  }
  &.__reverse {
    .overview-block__content {
      @include pc {
        flex-direction: row-reverse;
      }
    }
  }
  &__content {
    @include pc {
      display: flex;
      justify-content: space-between;
      margin: 0 -21px;
    }
  }
  &__column {
    @include pc {
      flex: 0 1 540px;
      margin: 0 21px;
    }
    p {
      margin: 22px 0 0;
      @include pc {
        line-height: 2;
        font-size: 14px;
      }
      @include mq {
        line-height: 2;
        font-size: 12px;
      }
      &:first-child {
        margin: 0;
      }
    }
  }
  .__figure {
    @include mq {
      margin: 20px 0 0;
    }
  }
  .overview-info {
    @include pc {
      margin: 22px 0 0;
      font-size: 14px;
    }
    @include mq {
      margin: 30px 0 0;
      font-size: 12px;
    }
    &:first-child {
      margin: 0;
    }
    a {
      text-decoration: none;
      padding-bottom: 0;
      border: none;
    }
    p {
      margin: 0;
      line-height: 2;
      @include pc {
        font-size: 14px;
      }
      @include mq {
        font-size: 12px;
      }
    }
    dl {
      border-top: 1px solid #e2e2e2;
      @include pc {
        display: flex;
        margin: 22px 0 0;
        padding: 22px 0 0;
      }
      @include mq {
        display: block;
        margin: 20px 0 0;
        padding: 20px 0 0;
      }
      &:first-child {
        margin: 0;
        border: none;
        padding: 0;
      }
    }
    dt {
      font-weight: bold;
      @include pc {
        flex: 0 0 75px;
      }
      @include mq {
        font-size: 14px;
      }
    }
    dd {
      @include pc {
        flex: 1 1 auto;
        margin: 0 0 0 20px;
      }
      @include mq {
        margin: 5px 0 0;
      }
    }
  }
}
