.filters-toolbar-wrapper {
  border: none;
  @include pc {
    margin: 0 0 25px;
  }
}
.filters-toolbar__item-wrapper {
  justify-content: flex-end;
}
.filters-toolbar__item-child {
  &:first-child {
    margin: 0;
    @include mq {
      width: 100%;
    }
  }
}
.filters-toolbar__input-wrapper {
  @include mq {
    display: block;
  }
}
.select-group select {
  height: 38px;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 16px;
  background-color: #f5f5f5;
  @include pc {
    width: 240px !important;
  }
  @include mq {
    width: 100% !important;
  }
}
.filters-toolbar {
  .icon-chevron-down {
    z-index: 10;
  }
}
.filters-toolbar__label {
  display: none;
}

.yamato-collection {
  li {
    flex: 1;
    @include pc {
      width: percentage(350px / 1122px);
      margin: 0 0 0 percentage(34px / 1122px);
      padding: 0;
    }
    @include mq {
      width: percentage(160px / 335px);
      margin: 0 0 0 percentage(15px / 335px);
    }
    &:nth-of-type(3n + 1) {
      @include pc {
        margin-left: 0;
      }
    }
    &:nth-of-type(2n + 1) {
      @include mq {
        margin-left: 0;
      }
    }
  }
  .yamato-product-card-info {
    text-align: center;
    @include pc {
      margin: 30px 0 0;
    }
    @include mq {
      margin: 15px 0 0;
    }
    > dt {
      @include heading-common;
      font-weight: bold;
      @include pc {
        font-size: 18px;
      }
      @include mq {
        font-size: 13px;
      }
    }
    > dd {
      margin: 0;
    }
    .price {
      justify-content: center;
      align-items: center;
      dd {
        margin: 0;
      }
    }
    .price__regular {
      margin: 0;
    }
    .price-item {
      font-weight: normal;
      @include pc {
        font-size: 16px;
      }
      @include mq {
        font-size: 12px;
      }
    }
  }
}
