.yamato-blog-list {
  @include pc {
    margin: 43px 0 0;
    font-size: 14px;
  }
  @include mq {
    margin: 29px 20px 0;
    font-size: 12px;
  }
  > li {
    &:first-child {
      .article__link {
        border-top: none;
      }
    }
  }
  .article__link {
    border-top: 1px solid #e2e2e2;
    @include pc {
      display: flex;
      align-items: center;
      padding: 22px 0;
      font-size: 14px;
    }
    @include mq {
      display: block;
      padding: 18px 0 16px;
      font-size: 12px;
    }
  }
  .article__prop {
    display: flex;
    align-items: center;
    @include pc {
      margin: 0 20px 0 0;
    }
  }
  .article__tag-icon {
    flex: 0 0 auto;
    font-weight: bold;
    text-align: center;
    @include pc {
      width: 90px;
      margin: 0;
      border-radius: 15px;
      padding: 6px;
    }
    @include mq {
      width: 72px;
      margin: 0;
      border-radius: 15px;
      padding: 3px 6px;
    }
    &.__new {
      background: #009b4a;
      color: #fff;
    }
    &.__info {
      background: #ff5611;
      color: #fff;
    }
  }
  .article__date,
  .article__title {
    margin: 0;
  }
  .article__date {
    @include pc {
      flex: 0 0 84px;
    }
    @include mq {
      flex: 0 0 60px;
      margin: 0 7px 0 0;
    }
  }
  .article__title {
    @include mq {
      display: block;
      margin: 15px 0 0;
    }
  }
}
