.rte {
    table {
        font-family: $font-sans-serif;
        border: 1px solid 1px solid map-get($color, table-border);
    }
    th,
    td {
        font-family: $font-sans-serif;
    }
    thead {
        th {
            border-color: map-get($color, white);
            background-color: var(--color-btn-primary);
            color: map-get($color, white);
            font-weight: 400;
        }
    }
    tbody {
        th,
        td {
            border-color: map-get($color, table-border);
        }
    }
}
