.contact-form {
  label {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    [aria-hidden] {
      display: block;
      height: 20px;
      overflow: hidden;
      margin: 0 0 0 0.5em;
      &::before {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 20px;
        padding: 0.25em 0.5em;
        border-radius: 5px;
        background: var(--color-btn-primary);
        color: #fff;
        font-size: 14px;
        content: '必須';
      }
    }
  }
}
