// $device-width が定義済みかどうかと map 型かどうかを判定する
// 返り値: $device-width-is-map
@function is-device-width-map() {
  $device-width-exists: variable-exists(device-width);
  $device-width-is-map: true;
  @if $device-width-exists and type-of($device-width) == 'map' {
    $device-width-is-map: false;
  }
  @return $device-width-is-map;
}

@function get-device-width($arg: null) {
  $preset-width: 1440px;
  $return-value: 0;
  $device-width-specified: is-device-width-map();

  @if $device-width-specified != true or type-of($arg) == 'null' {
    $return-value: $preset-width;
  } @else if type-of($arg) == 'number' {
    $return-value: $arg;
  } @else if type-of($arg) == 'string' and map-get($device-width, $arg) != null {
    $return-value: map-get($device-width, $arg);
  } @else if type-of($arg) == 'string' and map-get($device-width, $arg) == null {
    $return-value: $preset-width;
  } @else {
    $return-value: $preset-width;
  }
  @return $return-value;
}
