.blog-layout {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 80px 0 0;
  @media only screen and (max-width: 750px) {
    display: block;
  }
  &__eyecatch {
    flex: 0 1 480px;
    @media only screen and (min-width: 751px) {
      margin: 0 40px 0 0;
    }
    &-featured__images {
      position: relative;
      max-width: 480px;
      width: 100%;
      height: 360px;
    }
    &-featured__image {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      overflow: hidden;
      opacity: 0;
      transition: opacity 0.3s linear 0s;
      &.__is-current {
        opacity: 1;
      }
    }
    &-featured__stage {
      display: flex;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
      background: #333;
      img {
        @include object-fit;
        width: 100%;
        height: 100%;
      }
    }
    &-list {
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: flex-start;
      margin: 5px 0 0;
      padding: 0;
      opacity: 0;
      transition: opacity 0.3s linear 0s;
      &.__enabled {
        opacity: 1;
      }
      li {
        position: relative;
        flex: 0 1 calc((100% - 15px) / 4);
        margin: 5px 0 0 5px;
        padding: calc((100% - 15px) / 4) 0 0;
        cursor: pointer;
        @include pc {
          transition: all 0.3s linear 0s;
          &:hover {
            opacity: 0.8;
          }
        }
        &:nth-of-type(-n + 4) {
          margin-top: 0;
        }
        &:nth-of-type(4n + 1) {
          margin-left: 0;
        }
        &::after {
          box-sizing: border-box;
          position: absolute;
          left: 0;
          top: 0;
          display: block;
          width: 100%;
          height: 100%;
          border: 2px solid transparent;
          content: '';
          transition: all 0.3s linear 0s;
        }
        &.__is-current {
          &::after {
            border-color: var(--color-btn-primary);
          }
          span {
            box-sizing: border-box;
            overflow: hidden;
            &::after {
              box-sizing: border-box;
              position: absolute;
              left: 0;
              top: 0;
              display: block;
              width: 100%;
              height: 100%;
              border: 4px solid #fff;
              content: '';
            }
          }
        }
        span {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: auto;
          overflow: hidden;
        }
        img {
          position: absolute;
          top: 0;
          left: 0;
          object-fit: cover;
          max-width: none;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  &__content {
    flex: 1;
    min-width: 300px;
    @media only screen and (max-width: 750px) {
      margin: 40px 0 0;
    }
    &:first-child {
      margin: 0;
    }
    .article__title {
      margin: 0;
    }
    .article__title-main {
      margin: 0;
    }
    .rte {
      margin-top: 36px 0 0;
      img {
        &[style*='left'] {
          margin: 0 15px 40px 0;
        }
        &[style*='right'] {
          margin: 0 0 40px 15px;
        }
      }
      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        clear: both;
      }
      h1,
      h2,
      h3 {
        margin: 1em 0 0;
        font-size: 18px;
        font-weight: 700;
        &:first-child {
          margin: 0;
        }
      }
      p,
      div[style*='align'],
      .scrollable-wrapper {
        margin: 18px 0 0;
        &:first-child {
          margin: 0;
        }
      }
      table {
        border-collapse: collapse;
        border: none;
        th,
        td {
          border: 1px solid #ccc;
          border-width: 0 0 1px;
          p {
            margin: 0;
          }
        }
      }
    }
  }
  .__append-image {
    display: none;
  }
}
