// ---------------------------------------
//    Coupon Style
// ---------------------------------------
@mixin desktop {
  @include mq(handheld, min) {
    @content;
  }
}
@mixin handheld {
  @include mq(handheld) {
    @content;
  }
}

%coupon-transition {
  transition: all 0.3s ease-out 0s;
}

%coupon-shadow {
  @include handheld {
    box-shadow: 0 0 5px rgba(#000, 0.3);
    border-radius: 10px;
    background-color: #fff;
  }
}
.coupon {
  @include desktop {
    margin: 40px 0 0;
    border: 2px solid #ff5611;
    border-radius: 10px;
    padding: 1em;
    background-color: #fff;
  }
  @include handheld {
    position: absolute;
    top: 100%;
    width: 100%;
  }
}
.coupon__container {
  @extend %coupon-transition;
  margin: 0;
  @include handheld {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    font-size: 13px;
    opacity: 0.7;
  }
  dt {
    @extend %coupon-transition;
    font-weight: 700;
    @include desktop {
      color: #ff5611;
      text-align: center;
    }
    @include handheld {
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 2em;
      border: 2px solid #ff5611;
      border-radius: 1em;
      padding: 0 1em;
      background-color: #ff5611;
      color: #fff;
    }
    svg {
      @include desktop {
        display: none;
      }
      @extend %coupon-transition;
      fill: #fff;
      margin: 0 0 0 0.5em;
    }
  }
  dd {
    margin: 10px 0 0;
    padding: 0;
    @include handheld {
      flex: 1 1 auto;
      display: none;
      width: 100%;
    }
  }
}
.coupon__display {
  @extend %coupon-shadow;
  position: relative;
  display: flex;
  @include desktop {
    flex-direction: column;
  }
}
.coupon__code {
  @extend %basic-field;
  flex: 0 1 auto;
  border: 1px solid #ccc;
  @include desktop {
    border-width: 1px 1px 0;
  }
  @include handheld {
    border-width: 1px 0 1px 1px;
    border-radius: 10px 0 0 10px;
  }
}
.coupon__clipboard {
  flex: 1 0 auto;
  display: flex;
  border: none;
  background-color: #ff5611;
  color: #fff;
  @include desktop {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0.75em 1em;
    font-size: 13px;
    font-weight: 700;
  }
  @include handheld {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 0 10px 10px 0;
    padding: 1em 2em 0.5em;
    font-size: 11px;
  }
  svg {
    fill: #fff;
    @include desktop {
      width: 1.5em;
      height: 1.5em;
      margin: 0 0.5em 0 0;
    }
    @include handheld {
      width: 100%;
      height: 2.5em;
      margin: 0 0 0.5em;
    }
  }
}
.coupon__tooltip {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  width: 9em;
  height: 2.6em;
  margin: auto;
  border-radius: 6px;
  background-color: #333;
  color: #fff;
  line-height: 1;
  text-decoration: none;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  &-content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
}
.coupon__aside {
  @extend %coupon-shadow;
  margin: 10px 0 0;
  @include handheld {
    padding: 1em 0;
  }
}
.coupon__description {
  p {
    margin: 10px 0 0;
    font-size: 14px;
    &:first-child {
      margin: 0;
    }
    @include handheld {
      padding: 0 1em;
    }
  }
}
.coupon__help {
  &:first-child {
    margin-top: 0;
    border-top: none;
  }
  @include desktop {
    margin: 20px 0 0;
  }
  @include handheld {
    margin: 10px 0 0;
    border-top: 1px solid #ccc;
    padding: 1em 1em 0;
  }
}
.coupon__help-link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  color: #ff5611;
  &:link:not([disabled]) {
    &:hover,
    &:active,
    &:focus {
      color: #ff5611 !important;
      text-decoration: underline;
    }
  }
  svg {
    margin: 0 0.25em 0 0;
    fill: #ff5611;
  }
}
.__is-show {
  .coupon__container {
    @include handheld {
      opacity: 1;
    }
    dt {
      @include handheld {
        background-color: #fff;
        color: #ff5611;
      }
      svg {
        @include handheld {
          transform: rotate(180deg);
          fill: #ff5611;
        }
      }
    }
  }
}
