.collection-hero {
  @include pc {
    margin-top: 0;
  }
  @include mq {
    margin-top: 0;
  }
  &__image {
    height: 450px;
  }
  &__title-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(#5e4032, 0.8);
    @include pc {
      padding: 30px 30px 25px;
    }
    @include mq {
      right: 0;
      padding: 20px;
    }
    &::before {
      content: none;
    }
  }
  &__title {
    position: relative;
    top: 0;
    margin: 0;
    text-align: center;
    font-family: $font-sans-serif;
    transform: none;
    letter-spacing: 0.06em;
    @include pc {
      font-size: 32px;
    }
    @include mq {
      font-size: 28px;
    }
    small {
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      text-align: center;
      font-size: 8px;
      @include pc {
        margin: 14px 0 0;
      }
      @include mq {
        margin: 10px 0 0;
      }
      &::before,
      &::after {
        display: block;
        width: 26px;
        height: 1px;
        margin: 0 8px;
        background: #fff;
        content: '';
      }
    }
  }
}
.yamato-collection-template {
  > .grid__item {
    margin-top: 0;
    &:nth-child(n + 3) {
      @include mq {
        margin-top: 30px;
      }
    }
    &:nth-child(n + 4) {
      @include pc {
        margin-top: 60px;
      }
    }
  }
  .yamato-product-card {
    a {
      .yamato-product-card-container {
        @include pc {
          transition: opacity 0.3s ease-out 0s;
        }
      }
      &:hover {
        img {
          @include pc {
            opacity: 1;
          }
        }
        .yamato-product-card-container {
          @include pc {
            opacity: 0.5;
          }
        }
      }
    }
  }
  .yamato-product-card-container {
    position: relative;
    .icon-organic-jas {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      z-index: 10;
      @include pc {
        width: 20%;
      }
      @include mq {
        width: 20%;
      }
      img {
        display: block;
        width: 100%;
      }
    }
    .icon-rice-set {
      position: absolute;
      left: 5px;
      bottom: 5px;
      display: block;
      z-index: 10;
      @include pc {
        width: 23.85%;
      }
      @include mq {
        width: 23.125%;
      }
      img {
        display: block;
        width: 100%;
      }
    }
  }
  .yamato-product-card-image-wrapper {
    position: relative;
    width: 100%;
    height: 0;
    @include pc {
      padding: 300px / 350px * 100% 0 0;
    }
    @include mq {
      padding: 137px / 160px * 100% 0 0;
    }
    img {
      @include object-fit;
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .yamato-product-card-info {
    text-align: center;
    @include pc {
      margin: 30px 0 0;
    }
    @include mq {
      margin: 15px 0 0;
    }
    > dt {
      @include heading-common;
      font-weight: bold;
      @include pc {
        font-size: 18px;
      }
      @include mq {
        font-size: 13px;
      }
    }
    > dd {
      margin: 0;
      &.sp-margin {
        @include pc {
          display: none;
        }
      }
    }
    .price {
      justify-content: center;
      align-items: center;
      dd {
        margin: 0;
      }
    }
    .price__regular {
      margin: 0;
    }
    .price-item {
      font-weight: normal;
      @include pc {
        font-size: 16px;
      }
      @include mq {
        font-size: 12px;
      }
    }
  }
}
