.nav-global {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  width: 0;
  visibility: hidden;
  overflow: hidden;
  z-index: 100;
  .nav-global-content {
    overflow: auto;
  }
  ul {
    @extend %list-reset;
  }
  #nav-trigger:checked + & {
    width: 400px;
    visibility: visible;
  }
}
