%login-layout {
  margin-top: 100px;
  @include mq(handheld) {
    margin-top: 50px;
  }
}
// Cart View
.template-cart {
  #PageContainer {
    @extend %login-layout;
  }
}
// Login View
.template-customers-login {
  #CustomerLoginForm {
    @extend %login-layout;
  }
}
// MyPage View
.template-customers-account {
  #PageContainer {
    @extend %login-layout;
  }
  .section-header {
    h1 + div > .ui-button.btn.btn--subdued.btn--size-small {
      height: 36px;
      padding: 0;
      overflow: hidden;
      &::before {
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 36px;
        padding: 8px;
        content: '定期購入履歴';
      }
    }
  }
  .myaccount__account-details {
    & + div > a[href*='manage-subscription'] {
      margin: 0 0 0 30px;
      @include mq(handheld) {
        margin: 0 0 0 22px;
      }
      > span {
        display: block;
        height: 18px;
        overflow: hidden;
        &::before {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 18px;
          content: '定期購入の管理';
        }
      }
    }
  }
}
