.product-template__container {
  .product-single__title {
    font-family: $font-sans-serif;
    font-weight: bold;
    letter-spacing: 0.06em;
    @include pc {
      margin: 15px 0 0;
      font-size: 28px;
    }
    @include mq {
      margin: 30px 0 0;
      font-size: 20px;
    }
  }
  .price-item {
    font-weight: normal;
    @include pc {
      font-size: 28px;
    }
    @include mq {
      font-size: 20px;
    }
    small {
      @include pc {
        font-size: 16px;
      }
      @include mq {
        font-size: 12px;
      }
    }
  }
  .product-form {
    border-top: 1px solid #e2e2e2;
    @include pc {
      margin: 40px 0 0;
      padding: 40px 0 0;
    }
    @include mq {
      margin: 30px 0 0;
      padding: 30px 0 0;
    }
  }
  .product-form__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    > label {
      margin-right: 18px;
      font-size: 20px;
    }
  }
  .product-form__cart-submit {
    @include heading-common;
    border-radius: 40px;
    background: var(--color-btn-primary);
    color: #fff;
    @include pc {
      border-color: #ff5611;
      padding: 24px;
      font-size: 18px;
      transition: all 0.3s ease-out 0s;
      transition-property: background-color, color;
    }
    @include mq {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 18px;
      font-size: 16px;
    }
    &:hover {
      @include pc {
        background-color: #fff;
        color: #ff5611;
      }
    }
  }
  .product-single__description {
    border-top: 1px solid #e2e2e2;
    line-height: 2;
    @include pc {
      margin: 40px 0 0;
      padding: 30px 0 0;
      font-size: 14px;
    }
    @include mq {
      margin: 30px 0 0;
      padding: 30px 0 0;
      font-size: 12px;
    }
  }
  .product-reviews-icon {
    text-align: center;
    @include pc {
      margin: 110px 0 0;
    }
    @include mq {
      margin: 50px 0 0;
    }
  }
  #shopify-product-reviews {
    margin: 0;
    .spr-container {
      padding: 0 !important;
      border: none !important;
    }
    .spr-header-title {
      @include heading-common;
      text-align: center;
      @include pc {
        font-size: 28px;
      }
      @include mq {
        font-size: 20px;
      }
      &::after {
        display: block;
        color: var(--color-btn-primary);
        text-transform: uppercase;
        content: 'review';
        @include pc {
          margin: 11px 0 0;
          letter-spacing: 0;
          font-size: 14px;
        }
        @include mq {
          margin: 11px 0 0;
          letter-spacing: 0.05em;
          font-size: 11px;
        }
      }
    }
    .spr-summary {
      @include mq {
        margin: 33px 0 0;
      }
    }
    .spr-summary-caption {
      @include pc {
        font-size: 18px;
      }
      @include mq {
        font-size: 14px;
      }
    }
    .spr-summary-actions-newreview {
      color: #ff5611;
      text-decoration: underline;
      font-weight: bold;
      @include pc {
        font-size: 21px;
      }
      @include mq {
        display: block;
        margin: 30px 0 0;
        font-size: 15px;
      }
    }
    .spr-review-header-title {
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 0.06em;
    }
    .spr-review-header-byline {
      margin: 0;
      font-size: 12px;
      font-style: normal;
      color: #261204;
      opacity: 1;
      strong {
        margin-left: 1em;
        font-weight: normal;
        &:first-child {
          margin-left: 0;
        }
      }
    }
    .spr-review-content {
      margin: 10px 0 0;
    }
    .spr-review-content-body {
      font-size: 14px;
      line-height: 2;
      letter-spacing: 0.06em;
    }
    .spr-form-actions {
      text-align: center;
      @include pc {
        margin: 50px 0 0;
      }
    }
    .spr-form-title {
      display: none;
    }
    .spr-form-contact,
    .spr-form-review {
      > div {
        border-top: 1px solid #e2e2e2;
        @include pc {
          display: flex;
          margin: 20px 0 0;
          padding: 20px 0 0;
        }
        @include mq {
          display: block;
          margin: 15px 0 0;
          padding: 15px 0 0;
        }
      }
      .spr-form-contact-name {
        border-top: none;
        margin: 0;
        padding: 0;
      }
    }
    .spr-form-label {
      letter-spacing: 0.06em;
      line-height: 2;
      font-weight: bold;
      @include pc {
        flex: 0 0 180px;
        font-size: 16px;
      }
      @include mq {
        font-size: 13px;
      }
    }
    .spr-form-input {
      @include pc {
        flex: 1 1 auto;
      }
    }
    .spr-button-primary {
      @include heading-common;
      float: none;
      border-radius: 40px;
      @include pc {
        padding: 23px;
        font-size: 18px;
        width: 100%;
        max-width: 400px;
      }
    }
  }
  .product-single__thumbnail {
    height: 100%;
    img {
      @include object-fit;
      width: 100%;
      height: 100%;
    }
  }
  .product-single__media-wrapper {
    position: relative;
    max-width: none !important;
    height: 380px;
    overflow: hidden;
    img {
      @include object-fit;
      width: 100% !important;
      height: 100% !important;
      max-width: none !important;
      max-height: none !important;
    }
    .icon-organic-jas {
      position: absolute;
      display: block;
      left: 0;
      top: 0;
      z-index: 10;
      @include pc {
        width: 16.76%;
      }
      @include mq {
        width: 16.72%;
      }
      img {
        display: block;
        width: 100%;
      }
    }
    .icon-rice-set {
      position: absolute;
      display: block;
      bottom: 5px;
      left: 5px;
      z-index: 10;
      @include pc {
        width: 16.76%;
      }
      @include mq {
        width: 16.72%;
      }
      img {
        display: block;
        width: 100%;
      }
    }
    &:nth-of-type(n + 2) {
      .icon-rice-set {
        display: none;
      }
    }
  }
  .product-single__thumbnails-item-slide {
    @include mq {
      height: 90px;
    }
  }
}
