.yamato-guide {
    .guide-heading-a01 {
        @include heading-common;
        color: var(--color-btn-primary);
        font-family: $font-sans-serif;
        font-weight: bold;
        @include pc {
            margin: 70px 0 0;
            font-size: 21px;
        }
        @include mq {
            margin: 40px 0 0;
            font-size: 15px;
        }
        &:first-child {
            margin: 0;
        }
    }
    .guide-heading-b01 {
        @include heading-common;
        font-weight: bold;
        @include pc {
            margin: 30px 0 0;
            font-size: 16px;
        }
        @include mq {
            margin: 20px 0 0;
            font-size: 14px;
        }
        &:first-child {
            margin: 0;
        }
    }
    .guide-text {
        line-height: 1.7;
        @include pc {
            margin: 20px 0 0;
            font-size: 14px;
        }
        @include mq {
            margin: 15px 0 0;
            font-size: 12px;
        }
    }
    .guide-figure {
        @include pc {
            max-width: 397px;
            margin: 40px 0 0;
        }
        @include mq {
            margin: 20px 0 0;
        }
        img {
            vertical-align: bottom;
        }
    }
    .guide-note-list {
        list-style: none;
        @include pc {
            margin: 20px 0 0;
            font-size: 14px;
        }
        @include mq {
            margin: 10px 0 0;
            font-size: 14px;
        }
    }
    .guide-table {
        box-sizing: border-box;
        border: none;
        border-collapse: collapse;
        @include mq(handheld, min) {
            width: calc(100% - 2px);
            max-width: 500px;
            margin: 20px 0 0;
            font-size: 14px;
        }
        @include mq {
            width: calc(100% - 2px);
            margin: 20px 0 0;
            font-size: 12px;
        }
        th,
        td {
            font-family: $font-sans-serif;
            font-weight: normal;
        }
        thead {
            th {
                border: 1px solid map-get($color, white);
                border-width: 0 0 0 1px;
                background-color: var(--color-btn-primary);
                color: map-get($color, white);
                &:first-child {
                    border: none;
                    @include pc {
                        width: 160px;
                    }
                }
            }
        }
        tbody {
            th,
            td {
                border: 1px solid #a0a0a0;
            }
        }
        thead + tbody {
            tr {
                &:first-child {
                    th,
                    td {
                        border-top: none;
                    }
                }
            }
        }
    }
    .guide-define-list {
        @include pc {
            margin: 20px 0 0;
            font-size: 14px;
        }
        @include mq {
            font-size: 12px;
            margin: 15px 0 0;
        }
        a {
            padding: 0;
            border: none;
        }
        dl {
            display: flex;
            margin: 0;
        }
        dt {
            flex: 0 0 7em;
            margin: 0;
        }
        dd {
            flex: 1 1 auto;
            margin: 0;
        }
    }
}
