.popular-list {
  counter-reset: side-popular;
  @include mq(handheld, min) {
    margin: 16px 0 0;
  }
  li {
    counter-increment: side-popular;
    position: relative;
    @include mq(handheld, min) {
      margin: 28px 0 0;
    }
    .icon-popular {
      position: absolute;
      top: 0;
      left: 0;
      &::before {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        padding: 10px 0;
        font-size: 16px;
        z-index: 10;
        content: counter(side-popular);
      }
      .icon-popular-footer {
        &::before,
        &::after {
          position: absolute;
          top: 100%;
          display: block;
          border-color: transparent;
          border-style: solid;
          content: '';
        }
        &::before {
          left: 0;
          border-width: 0 0 5px 15px;
        }
        &::after {
          right: 0;
          border-width: 0 15px 5px 0;
        }
      }
    }
    &:first-child {
      span.icon-popular {
        &::before {
          background-color: map-get($color, prize1st);
          color: map-get($color, white);
        }
        .icon-popular-footer {
          &::before {
            border-left-color: map-get($color, prize1st);
          }
          &::after {
            border-right-color: map-get($color, prize1st);
          }
        }
      }
    }
    &:nth-child(2) {
      span.icon-popular {
        &::before {
          background-color: map-get($color, prize2nd);
          color: map-get($color, white);
        }
        .icon-popular-footer {
          &::before {
            border-left-color: map-get($color, prize2nd);
          }
          &::after {
            border-right-color: map-get($color, prize2nd);
          }
        }
      }
    }
    &:nth-child(3) {
      span.icon-popular {
        &::before {
          background-color: map-get($color, prize3rd);
          color: map-get($color, white);
        }
        .icon-popular-footer {
          &::before {
            border-left-color: map-get($color, prize3rd);
          }
          &::after {
            border-right-color: map-get($color, prize3rd);
          }
        }
      }
    }
  }
}
.product-card--list-tiny {
  @include pc {
    transition: opacity 0.3s ease-out 0s;
  }
  &:hover {
    @include pc {
      opacity: 0.5;
    }
  }
  .yamato-product-card-container {
    position: relative;
    .icon-organic-jas {
      @include pc {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 20%;
        z-index: 10;
      }
      img {
        display: block;
        width: 100%;
      }
    }
    .icon-rice-set {
      @include pc {
        position: absolute;
        left: 5px;
        bottom: 5px;
        display: block;
        width: 17.73%;
        z-index: 10;
      }
      img {
        display: block;
        width: 100%;
      }
    }
  }
  .list-view-item__image-wrapper {
    position: relative;
    margin: 0;
    overflow: hidden;
    @include mq(handheld, min) {
      height: 140px;
    }
    img {
      @include object-fit;
      width: 100%;
      max-width: none;
      height: 100%;
      max-height: none;
    }
  }
  .list-view-item__title {
    @include mq(handheld, min) {
      margin: 12px 0 0;
      font-size: 16px;
    }
  }
  .product-card__title {
    border-bottom: none;
    font-weight: normal;
    @include mq(handheld, min) {
      font-size: 16px;
    }
  }
}
