// utility nav
.utility-nav {
  font-size: 16px;
  @include mq(handheld, min) {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
  }
  @include mq(handheld) {
    position: static;
  }
  ul {
    display: flex;
    @include mq(handheld, min) {
      align-items: stretch;
      border-radius: 0 0 0 10px;
      background-color: map-get($color, white);
    }
    overflow: hidden;
    @include mq(handheld) {
      flex-wrap: wrap;
    }
  }
  li {
    flex-grow: 0;
    flex-shrink: 1;
    border-style: solid;
    border-color: #e2e2e2;
    @include mq(handheld, min) {
      flex-basis: 100%;
      display: flex;
      justify-content: stretch;
      align-items: stretch;
      border-width: 0 0 0 1px;
      white-space: nowrap;
      &:first-child {
        border-left: none;
      }
    }
    @include mq(handheld) {
      flex-basis: 50%;
      border-width: 0 0 1px;
      &:nth-child(2n) {
        border-width: 0 0 1px 1px;
      }
      &:nth-child(2n + 1) {
        &:last-child {
          flex-basis: 100%;
          border-width: 0 0 1px;
        }
      }
    }
  }
  a {
    display: flex;
    align-items: center;
    @include mq(handheld, min) {
      padding: 15px 17px;
      transition: all 0.3s ease-out 0s;
      transition-property: background-color, color;
      background-color: #fff;
      &:hover {
        background-color: #ff5611;
        color: #fff;
        .utility-nav__icon {
          svg {
            &.orange-account,
            &.orange-signin,
            &.orange-signout,
            &.orange-bag,
            &.orange-mail {
              fill: #fff;
            }
            &.orange-cart {
              fill: #fff;
              .cart-roller {
                fill: #fff;
              }
            }
          }
        }
      }
    }
    @include mq(handheld) {
      justify-content: center;
      padding: 12px;
      white-space: nowrap;
    }
    .utility-nav__icon {
      svg {
        &.orange-signin,
        &.orange-signout,
        &.orange-account,
        &.orange-bag,
        &.orange-mail {
          fill: #ff8900;
        }
        &.orange-cart {
          fill: #ffa311;
          .cart-roller {
            fill: #383737;
          }
        }
      }
    }
  }
  .utility-nav__label {
    margin-left: 4px;
  }
  ins {
    text-decoration: none;
    font-size: 13px;
  }
}
