$devices: desktop, handheld, phone;
$device-width: (
    desktop: 1366px,
    handheld: 750px,
    phone: 375px,
);

// color
$color: (
    body-text: #261204,
    attention: #dc000c,
    sub: #07204a,
    section: #fff5ed,
    shadow: rgba(#000000, 0.8),
    white: #fff,
    gray: #868686,
    table-border: #a0a0a0,
    prize1st: #deb64c,
    prize2nd: #8c8f90,
    prize3rd: #896b57,
);

// font-family
$font-family: 'Noto Serif JP', serif;
$font-sans-serif: 'Helvetica', 'Arial', 'YuGothic', 'Yu Gothic', sans-serif;

// font-size
$basic-font-size: (
    desktop: 15px,
    handheld: 13px,
);

// vidro
$vidro-color: #4caba5, #e8ce86, #db8289, #8fbf9b, #c2a0c0;
$vidro-angle: -100deg, 150deg, 350deg, 320deg, 90deg;

// easing
$easing: cubic-bezier(0.47, 0.04, 0.29, 1.03);
